<template>
  <div class="mylikeview">
    <van-sticky>
      <div class="mylikeTop">
        <span class="iconfont icon-xiangzuojiantou" @click="goPrev"></span>
        <p>我的喜欢</p>
        <div class="right"></div>
      </div>
    </van-sticky>
    <div class="works">
      <div class="worksRuslt">
        <div v-if="worksList">
          <router-link
            tag="div"
            :to="'/videoplay?id=' + item.articleId"
            class="videoInfo"
            v-for="item in worksList"
            :key="item.articleId"
          >
            <div class="Bigimg">
              <img :src="item.cover" alt="" />
            </div>
            <div class="detailInfo">
              <h4>{{ item.title }}</h4>
              <div class="count" v-if="item.count">
                播放 {{ item.count.count_view }} · 喜欢
                {{ item.count.count_like }}
              </div>
            </div>
          </router-link>
        </div>
        <div v-else class="nomore">还没有喜欢的作品~</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      worksList: null,
      loginPhone: "",
    };
  },
  methods: {
    getLikelist() {
      let userMsg = localStorage.getItem("userlist");
      let userlist = JSON.parse(userMsg);
      let index = userlist.findIndex((item) => item.type == "login");
      this.loginPhone = userlist[index].phone;
      let likemsg = localStorage.getItem("likeList");
      if (!likemsg) {
        return;
      }
      let likelist = JSON.parse(likemsg);
      let likeIndex = likelist.findIndex((item) => item.phone == this.loginPhone);
      if(likeIndex==-1){return}
      this.worksList = likelist[likeIndex].articleList;
    },
    goPrev(){
        this.$router.go(-1)
    }
  },
  created() {
    this.getLikelist();
  },
};
</script>

<style lang="less">
.mylikeview {
  .mylikeTop {
    width: 100%;
    height: 42px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    border-bottom: 1px solid #f7f7f7;
    .iconfont {
      font-size: 15px;
      font-weight: bold;
      padding-left: 20px;
    }
    p {
      font-size: 16px;
    }
    .right {
      width: 15px;
      height: 15px;
      padding-right: 20px;
    }
  }
  .works {
    .worksRuslt {
      padding: 0 20px;
      .videoInfo {
        padding: 15px 0;
        display: flex;
        border-bottom: 1px solid #f7f7f7;
        .Bigimg {
          width: 160px;
          height: 90px;
          border-radius: 5px;
          overflow: hidden;
          box-shadow: 0px 4px 4px 0px #ddd;
          img {
            width: 160px;
            height: 90px;
          }
        }
        .detailInfo {
          flex: 1;
          padding-left: 12px;
          h4 {
            font-size: 13px;
            line-height: 19px;
            white-space: wrap;
            font-weight: bold;
          }
          .count {
            height: 26px;
            line-height: 26px;
            font-size: 12px;
            color: #b0b0b0;
          }
        }
      }
      .nomore {
          margin-top: 100px;
          text-align: center;
          font-size: 14px;
        //   color: #aaa;
      }
    }
  }
}
</style>